<template>
  <v-container fluid fill-height>
    <v-layout align-center justify-center>
      <v-flex xs12 sm8 md4 lg6>
        <form ref="approveForm" method="post" action="/oauth/authorize">

          <v-card class="mx-auto pa-6" max-width="400">
            <v-card-text>


              <div class="layout column mb-4">
                <h3>应用 <a> {{ clientDetail.clientName }} </a> 将获取以下权限：</h3>
              </div>

              <v-divider></v-divider>

              <v-checkbox v-model="checkAll" label="全选"></v-checkbox>


              <v-divider></v-divider>

              <v-checkbox v-for="item in scopes"
                          :key="item"
                          :name="'scope.'+item"
                          v-model="selected"
                          :label="item"
                          :value="true"
              ></v-checkbox>
              <input style="display:none" name="user_oauth_approval" value="true"/>
              <input style="display:none" name="authorize" value="Authorize"/>

            </v-card-text>
            <v-card-actions>
              <v-btn color="info" type="submit" :loading="loading">
                同意授权
              </v-btn>
              <v-btn text color="info">
                取消
              </v-btn>

            </v-card-actions>
          </v-card>
        </form>
      </v-flex>
    </v-layout>
  </v-container>
</template>
<script>

export default {
  name: "Authorization",
  /**
   * user_oauth_approval: true
   scope.resources: true
   scope.role: true
   scope.userinfo: true
   authorize: Authorize
   */
  data() {
    return {
      loading: false,
      authorizationRequest: {},
      clientDetail: {},
      scopes: [],
      checkAll: false,
      selected: [],
    }
  },
  watch: {
    selected: {
      deep: true,
      handler: function () {
        this.checkAll = (this.scopes.length === this.selected.length)
      },
    },
    checkAll: {
      handler: function () {
        this.selected = this.checkAll ? this.scopes : this.selected
      },
    },
  },

  mounted() {
    this.$http.post("/oauth/confirm_access_info",).then(response => {
      this.authorizationRequest = response.data
      this.scopes = response.data.scope
      this.clientDetail = response.data.extensions.clientDetail
    })
  },

  methods: {

    submit() {

    },

  }
}
</script>
